/* global Office */

Office.onReady(() => {
  // If needed, Office.js is ready to be called.
});

/**
 * Shows a notification when the add-in command is executed.
 * @param event
 */
const action = (event: Office.AddinCommands.Event) => {
  const message: Office.NotificationMessageDetails = {
    type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
    message: "Performed action.",
    icon: "Icon.80x80",
    persistent: true,
  };

  // Show a notification message.
  Office.context.mailbox.item?.notificationMessages.replaceAsync(
    "ActionPerformanceNotification",
    message
  );

  // Be sure to indicate when the add-in command function is complete.
  event.completed();
}

// Register the function with Office.
Office.actions.associate("action", action);

let dialog: Office.Dialog | null = null;
const openLoginDialog = (event: { completed: () => void }) => {
  Office.context.ui.displayDialogAsync(
    "https://localhost:3000/assets/dialog.html",
    { height: 60, width: 40 },
    (asyncResult) => {
      if (asyncResult.status === Office.AsyncResultStatus.Failed) {
        console.error("Failed to open dialog:", asyncResult.error.message);
      } else {
        dialog = asyncResult.value;
        dialog.addEventHandler(
          Office.EventType.DialogMessageReceived,
          (arg) => handleDialogMessage(arg)
        );
      }
    }
  );

  event.completed();
};

const handleDialogMessage = async (arg: any) => {
  console.log("Raw message received:", arg.message);
  try {
    const data = JSON.parse(arg.message);
    console.log("Parsed data:", data);

    localStorage.setItem('bearer_token', data.data.session_info.bearer_token);
    localStorage.setItem('endpoint_id', data.data.session_info.endpoint_id);
    localStorage.setItem('license_key', data.data.session_info.license_key);
    localStorage.setItem('company_id', data.data.session_info.company_id);
    localStorage.setItem('expires_at', data.data.session_info.expires_at);
  } catch (e) {
    console.error("Failed to parse dialog message:", e);
  } finally {
    setTimeout(() => {
      if (dialog) {
        try {
          dialog.close();
          console.log("Dialog closed successfully.");
          dialog = null;
          callFunctionListApi();
        } catch (e) {
          console.error("Failed to close dialog:", e);
        }
      } else {
        console.error("Dialog is null or undefined, cannot close.");
      }
    }, 1000);
  }
};

const callFunctionListApi = async () => {
  const bearer_token = localStorage.getItem("bearer_token");
  const endpoint_id = localStorage.getItem("endpoint_id");
  const company_id = localStorage.getItem("company_id");

  try {
    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append("Authorization", `Bearer ${bearer_token}`);

    const raw = JSON.stringify({
      "company_id": company_id,
      "endpoint_id": endpoint_id,
      "format": "excel-manifest"
    });

    const requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
    };

    const response = await fetch(
      "https://zpdwekrejwwncdikouve.supabase.co/functions/v1/api-gateway/api/v1/function-list",
      requestOptions
    );

    if (!response.ok) {
      throw new Error(`API failed with status ${response.status}`);
    }

    const result = await response.json();
    const functions = result.data.functions;

    const paramObject = functions.reduce((acc: Record<string, string[]>, func: { name: string | number; parameters: any[]; }) => {
      acc[func.name] = func.parameters.map((p: { name: any; }) => p.name);
      return acc;
    }, {});

    const { fnName, parameters } = await getActiveCellParams(paramObject);

    if (fnName) {
      await callFunctionExecuteApi(parameters);
    };
  } catch (error) {
    console.error("API Error:", error);
  };
};

// Reusable function
const getActiveCellParams = async (paramObject: { [x: string]: string[]; }): Promise<{
  fnName: string | null;
  parameters: Record<string, string>;
}> => {
  const formula = '=ORF.SUMOFTHREE(1,2,3)';

  // Extract function name + args
  const match = formula.match(/=([A-Z0-9_.]+)\((.*)\)/i);
  if (!match) {
    console.error("No valid function in cell");
    return { fnName: null, parameters: {} };
  }

  const fnName = match[1];
  const args = match[2].split(",").map((a: string) => a.trim());

  // Build parameters object one by one
  let parameters: Record<string, string> = {};
  if (fnName && paramObject[fnName]) {
    paramObject[fnName].forEach((param: string, i: number) => {
      parameters[param] = args[i] || "";
    });
  }

  return { fnName, parameters };
};

const callFunctionExecuteApi = async (parameters: Record<string, string>) => {
  const bearer_token = localStorage.getItem("bearer_token");
  const endpoint_id = localStorage.getItem("endpoint_id");
  const company_id = localStorage.getItem("company_id");

  try {
    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append("Authorization", `Bearer ${bearer_token}`);

    const raw = JSON.stringify({
      "company_id": company_id,
      "endpoint_id": endpoint_id,
      "function_id": 9,
      "data": {
        "parameters": parameters
      },
      "timestamp": "2025-09-05T10:03:44.873Z"
    });

    const requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
    };

    const response = await fetch(
      "https://zpdwekrejwwncdikouve.supabase.co/functions/v1/api-gateway/api/v1/function-execute",
      requestOptions
    );

    if (!response.ok) {
      throw new Error(`API failed with status ${response.status}`);
    }

    const result = await response.json();
    console.log("API Success:", result);
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const cell = sheet.getRange("A1");
      cell.values = [["API call successful!"]];
      await context.sync();
    }).catch((error) => {
      console.error("Failed to write to cell A1:", error);
    });
  } catch (error) {
    console.error("API Error:", error);
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const cell = sheet.getRange("A1");
      cell.values = [[`API error: ${error}`]];
      await context.sync();
    }).catch((error) => {
      console.error("Failed to write to cell A1:", error);
    });
  };
};

Office.actions.associate("openLoginDialog", openLoginDialog);
